.App {
  font-family: sans-serif;
  text-align: center;
}
.container {
  background-color: #666;
  width: 100%;
  height: 500px;
}

.parentMenuDiv {
  width: 50px;
  height: calc(100% - (20px + 30px));
  background: white;
  transition: width 1s;
  right: 1px;
  position: absolute;
  float: right;
  top: 48px;
  z-index: 1100;
}
.expand {
  width: 860px;
}
.verticalBar {
  background-color: #479aea;
  float: left;
  height: 100%;
}
.menu-icon-onclick {
  background-color: rgb(87, 245, 95);
}
